export const pushButton = () => {
  return { type: 'pushButton' }
}
export const toggleUp = () => {
  return { type: 'toggleUp' }
}
export const toggleDown = () => {
  return { type: 'toggleDown' }
}
export const stop = () => {
  return { type: 'stop' }
}



